body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 0;
  background-color: #f5f5f5;
}

* {
  box-sizing: border-box;
}

.app-container {
  max-width: 500px;
  margin: 0 auto;
  padding: 15px;
  background-color: white;
  min-height: 100vh;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
}

h1, h2, h3, h4 {
  margin-top: 0;
  color: #333;
}

button {
  cursor: pointer;
  padding: 8px 16px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 1rem;
}

button:hover {
  background-color: #0069d9;
}

.btn-secondary {
  background-color: #6c757d;
}

.btn-secondary:hover {
  background-color: #5a6268;
}

input, select {
  padding: 8px;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
  margin-bottom: 10px;
}

.card {
  border: 1px solid #ddd;
  padding: 15px;
  margin-bottom: 15px;
  border-radius: 4px;
  background-color: white;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -10px;
}

.col {
  flex: 1;
  padding: 0 10px;
  min-width: 200px;
}

/* Weekly Summary Styles */
.weekly-summary {
  margin-bottom: 20px;
}

.summary-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.summary-stats {
  margin-bottom: 20px;
}

.stat-item {
  text-align: center;
  margin-bottom: 10px;
}

.stat-value {
  font-size: 1.5rem;
  font-weight: bold;
  color: #007bff;
}

.export-btn {
  width: 100%;
  margin-top: 10px;
}

/* Day Details Styles */
.day-details {
  margin-top: 20px;
}

.day-form {
  margin-top: 15px;
}

.form-group {
  margin-bottom: 15px;
}

.work-status-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-top: 5px;
}

.status-btn {
  flex: 1;
  min-width: 120px;
}

.status-btn.active {
  background-color: #28a745;
}

.button-group {
  display: flex;
  gap: 10px;
  margin-top: 20px;
}

.button-group button {
  flex: 1;
}

.day-summary {
  margin-top: 15px;
}

.summary-row {
  display: flex;
  justify-content: space-between;
  padding: 8px 0;
  border-bottom: 1px solid #eee;
}

.summary-row:last-child {
  border-bottom: none;
  margin-bottom: 15px;
}

/* Time Input Styles */
.time-input-group {
  border: 1px solid #eee;
  padding: 12px;
  border-radius: 4px;
  background-color: #f9f9f9;
}

.time-inputs {
  display: flex;
  gap: 15px;
  margin-top: 8px;
}

.time-input {
  flex: 1;
}

.time-input label {
  display: block;
  font-size: 0.9rem;
  margin-bottom: 5px;
  color: #555;
}

.time-input input {
  width: 100%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.hours-worked {
  margin-top: 10px;
  text-align: right;
  color: #28a745;
  font-size: 1.1rem;
}

@media (max-width: 600px) {
  .row {
    flex-direction: column;
  }
  
  .col {
    width: 100%;
  }
  
  .work-status-buttons {
    flex-direction: column;
  }
  
  .button-group {
    flex-direction: column;
  }
  
  .time-inputs {
    flex-direction: column;
    gap: 8px;
  }
} 